'use strict';

import mongoose from 'mongoose';
import {registerEvents} from './newscomment.events';
import {Schema} from 'mongoose';
var NewscommentSchema = new mongoose.Schema({
  newsId:{
  	type:Schema.ObjectId,
  	ref:'News'
  },
  user:{
  	type:Schema.ObjectId,
  	ref:'User'
  },
  email:{
    type:String
  },
  name:{
    type:String
  },
  image:{
    type:String
  },
  comment:{
  	type:Array
  },
  createdAt:{
  	type:Date,
  	default:Date.now
  }

});

registerEvents(NewscommentSchema);
export default mongoose.model('Newscomment', NewscommentSchema);
